import {
	format
} from '../../common/formatTime.js'
export default {
	data() {
		return {
			money: '',
			time: '',
			buyerNmae: '',
			type: '',
			name: '',
			detial: ""

		}
	},
	onLoad: function() {
		const nowDate = new Date();
		nowDate.setDate(nowDate.getDate());
		this.time = this.formatDateTime(nowDate)
	},
	methods: {
		formatDateTime(date) {
			return format.formatDateTime(date, "yyyy-MM-dd");
		},
		save() {
			const value = uni.getStorageSync('loginUser');
			if(value){
				uni.request({
					url: this.websiteUrl + '/api/createAccount',
					data: {
						money: this.money,
						time: this.time,
						buyerNmae: this.buyerNmae,
						name: this.name,
						type: this.type,
						detial: this.detial
					},
					method: 'POST',
					dataType: 'json',
					success: (res) => {
						console.log(res);
						if (res.data) {
							uni.showToast({
								title: "保存成功！",
								icon: 'none'
							});
								this.name = '',
								this.money = "",
								this.buyerNmae = '',
								this.type = '',
								this.detial = '',
								this.time = '',
								uni.navigateBack({
									delta:1
								})
						} else {
							uni.showToast({
								title: "保存失败！",
								icon: 'none'
							});
						}
					}
				})
			}else{
				uni.navigateTo({
					url:'../login/login'
				})
			}
			
		}
	}
}
